@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0; }

.title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: nowrap; }

.title__highlight {
  background-image: url(./static/highlight.svg);
  height: 40px;
  width: 4px; }

.title__h1 {
  margin-left: 12px;
  font-family: "TTLakes-Bold", sans-serif;
  font-size: 28px;
  line-height: 40px;
  color: #1b1c21; }

@media (min-width: 1120px) {
  .title__h1 {
    margin-left: 30px; } }

@media (min-width: 768px) {
  .title__h1 {
    margin-left: 20px; } }

.title__main {
  display: flex;
  align-items: center; }

.main__button--hidden {
  display: none; }

@media (min-width: 1120px) {
  .main__button--hidden {
    display: flex;
    align-items: center; }
  .header__right-menu--repair {
    margin-right: 5px; }
  .header__right-menu--repair {
    margin-right: 2em; }
  label {
    margin-right: 10px; }
  .title__main,
  .main__button--hidden {
    margin-top: 20px; } }
