@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0; }

.slider-container2 {
  overflow: hidden;
  width: 100%;
  padding: 0px 20px;
  margin-top: 24px; }

.swiper-wrapper2 {
  display: flex;
  width: 100%;
  height: 200px;
  margin-left: 30px; }

@media (min-width: 600px) {
  .swiper-wrapper2 {
    justify-content: space-between;
    margin: 0; } }

.swiper-wrapper__card2 {
  padding: 5px;
  border-radius: 5px;
  height: 160px;
  width: auto;
  color: #000;
  font-size: 16px;
  flex-shrink: 0;
  margin-bottom: 16px;
  background-color: #f8f8f8;
  font-family: "TTLakes-Regular", sans-serif; }

@media (min-width: 600px) {
  .swiper-wrapper__card2 {
    width: auto; } }

.swiper-wrapper-main__btn2 {
  width: 40px;
  height: 40px;
  border: 2px solid #ff3e79;
  border-radius: 20px;
  display: flex;
  align-items: center;
  margin: 70px 0px 0px 160px; }

.swiper-wrapper2__cont {
  width: 224px;
  height: 160px;
  background-color: #fff;
  padding: 16px;
  border: 1px solid #eaeaea; }

@media (min-width: 600px) {
  .swiper-wrapper2__cont {
    width: 240px; } }
