@font-face {
  font-family: "TTLakes-Regular";
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: normal; }

@font-face {
  font-family: "TTLakes-Medium";
  src: url(./fonts/TTLakes-Medium.ttf) format("truetype"); }

@font-face {
  font-family: "TTLakes-Bold";
  src: url(./fonts/TTLakes-Bold.ttf) format("truetype"); }

* {
  box-sizing: border-box; }

body {
  background-color: #f8f8f8;
  padding: 0;
  margin: 0; }

.content-p {
  text-align: left;
  font-size: 14px;
  border-bottom: 1px solid #bff6fa;
  max-width: 95%;
  margin-left: 16px;
  font-family: "TTLakes-Regular", serif;
  color: #1b1c21; }

.footer {
  color: #7e7e82;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0px;
  text-align: center;
  margin-top: 20px;
  font-family: "TTLakes-Regular", serif; }

.footer.foter__text-1 {
  margin-top: 50px;
  font-family: "TTLakes-Regular", serif; }
